body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1, h2, h3 {
  font-family: Montserrat-Regular;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'Montserrat';
  src: local('Montserrat'), url(/static/media/Montserrat-Regular.3fe868a1.ttf) format('truetype');
}

.App {
  text-align: center;
  /* background-color: rgb(30, 30, 51); */
  /* background-color: rgb(23, 23, 32); */
}
.container {
  width: 100%;
  height: 93vh;
  position: relative;
  background: url('/images/flying.jpeg') no-repeat center center/cover;
}

.container h1, .container h3 {
  color: black;
  position: relative;
}
.elements {
  /* width: 1350px;
  height: auto; 
  background-image: url("../images/wallpaper.jpg"); */
  margin: auto;
  margin-bottom: 50px;
  padding-top: 100px;
  padding-bottom: 180px;
  display: flex;
}

.elements h1 {
  margin: auto;
  padding-top: 175px;
  padding-bottom: 150px;
  color: white;
}

.elements img {
  width: 170px;
  -webkit-transform: translate(-500%, -50%);
          transform: translate(-500%, -50%);
  display: absolute;
  padding-top: 15vh;
}
.title-elements {
  margin: auto;
  margin-bottom: 50px;
  display: flex;
}

.title-elements h1 {
  margin: auto;
  padding-top: 200px;
  padding-bottom: 200px;
  color: white;
}
.project1 {
  margin-bottom: 50px;
  padding-bottom: 50px;
  contain: content;
  /* border-bottom-color: #DCD5FF; */
  /* border-style: solid;
  border-width: 1px; */
  /* background-color: rgb(30, 30, 51); */
  /* background-image: url("./images/background.jpg"); */
}

.project1-media {
  float: left;
  margin: auto 50px;
}

.project1-media img {
  width: 560px;
  height: 315px;
}

.project1-info {
  width: 560px;
  height: auto;
  position: relative;
  float: right;
  margin: 40px;
}

.project1-info p1 {
  position: absolute;
}

.project-info p2, p3 {
  position: absolute;
}
div.gallery {
  /*border: 1px solid #777;*/
  margin: 43px;
  width: 23.99999%;
  height: 500px;
  display: inline-block;
}

div.gallery:hover {
  border: 1px solid #ccc;
}

img {
  height: 70%;
}

h3, h5, p {
  color: white;
  padding: 20px;
}
